import {
	mapMutations
} from 'vuex';

export default {
	data() {
		return {
			mobile: '',
			password: '',
			logining: false
		}
	},
	onLoad() {

	},
	methods: {
		...mapMutations(['login']),
		inputChange(e) {
			const key = e.currentTarget.dataset.key;
			this[key] = e.detail.value;
		},
		navBack() {
			uni.navigateBack();
		},
		toRegist() {
			this.$api.msg('去注册');
		},
		async toLogin() {
			this.logining = true;
			uni.request({
				url: this.constVal.baseURL + '/client/login',
				method: 'POST',
				header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				data: {
					clientNum: "13322430900",
					clientPwd: "123456",
				},
				success: (res) => {
					if (res.data.code == 200) {
						this.login(res.data.data);
						uni.showToast({
							image: '../../static/icons/check.png',
							title: '登陆成功',
						})
						setTimeout(function() {
							uni.navigateBack();
						}, 1000);
					} else {
						uni.showToast({
							image: '../../static/icons/close.png',
							title: '手机号或密码错误'
						})
					}
				}
			})
			// const sendData = {
			// 	mobile,
			// 	password
			// };
			// const result = await this.$api.json('userInfo');
			// if(result.status === 1){
			// 	this.login(result.data);
			//  uni.navigateBack();  
			// }else{
			// 	this.$api.msg(result.msg);
			// 	this.logining = false;
			// }
		}
	},

}
